.timeline-date {
  position: relative;
  float: right;
}

.year-container {
  height: 100%;
  width: 100%;
}

.year-container > div {
  height: 100%;
  width: 100%;
  position: absolute;
  display: table;
}

.year-container > div > h6 {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}

.container {
  margin-top: 80px;
}

.house-list-link, .nav-link {
  text-decoration: none !important;
  color: inherit;
}

.currency-format {
  color: coral;
}